// page-active-custom
.pac {
    background: #fff;

    &__breadcrumb {
        display: flex;
        align-items: center;
        width: 100%;
        height: 40px;
        margin: 20px 0 0 0;
        padding: 0 25px;
        background: #fff;
    }

    &__all-categories {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        padding: 0 25px;
        background: #fff;

        .slider {
            overflow: hidden;
        }
    }
    .search {
        max-width: 426px;
        margin: 20px auto;
    }

    .wrapper-active-cards {
        display: block;
        width: 100%;
        background: $bg-wrapper-card-color;
    }

    .header-wrap-card__sorting {
        .sorting__btn {
            position: relative;
        }

        .ds-popover {
            position: absolute;
            top: 20px;
            right: -25px;
            z-index: 9999;
        }
    }
    @media (max-width: 992px) {
        .card-active-custom-desktop {
            display: none;
        }
    }
    @media (min-width: 993px) {
        .card-active-custom.tablet {
            display: none;
        }
    }
    @media (max-width: 769px)  {
        .card-active-custom.tablet {
            display: none;
        }
        .header-wrap-card__tabs {
            display: none;
        }
    }
    @media (min-width: 769px) {
        .card-active-custom__wrap.mobile {
            display: none;
        }
    }
}

//footer
footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-img {
    display: flex;
    width: 100%;
    min-height: 100%;
    background: url("~images/footer_active.png");
}

.pagination-container {
    display: flex;
    margin-top: 100px;

}


