.error-pages {
    display: flex;
    flex-direction: column;
    background: $background-color-primary;
    padding: 0 20px;
    &__header-mobile {
        display: none;
        justify-content: flex-start;
        width: 100%;
    }
    &__container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    &__left-col {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        padding: 20px 0 45px 0;
        margin: 0;
        h1 {
            margin: 22px 0 5px 0;
            font-size: 32px;
            color: $color-text-heading;
            font-weight: normal;
        }
        h3 {
            margin: 20px 0 12px 0;
            font-weight: normal;
            font-size: 20px;
            color: $color-text-heading;
        }
    }
    &__right-col {
        display: flex;

    }
    &__img {
        max-width: 465px;
    }
    &__txt {
        font-size: 16px;
        font-style: normal;
        line-height: 28px;
        color: $color-text-heading;
        &-contacts {
            font-size: 14px;
        }
    }
    &__link {
        color: $color-primary;
        &_bold {
            font-weight: 600;
        }
    }
    // Разделительная линия
    .vm {
        display: block;
        &-40 {
            min-height: 40px; 
        }
        &-15 {
            min-height: 15px;
        }
    }

